.page4-content{
    .flex-box{
        display: flex;
        margin-bottom: 40px;
        @include pad(){
            padding-left: 16px;
        }
        h3{
            margin-right: 16px;
        }
        .selector{
            height: 38px;
            width: 304px;
        }
    }
    .row{
        @include flex-row;
        flex-wrap: wrap;
        justify-content: flex-start;
        @include pad(){
            justify-content: space-between;
            padding-right: 16px;
            padding-left: 16px;
        }
        .item{
            border: 1px solid #00000020;
            width: 32%;
            margin-right: 14px;
            margin-bottom: 48px;
            @include pad(){
                width: 48%;
                margin-right: 0px;
            }
            @include phone(){
                width: 100%;
            }
            h3{
                font-size: 24px;
                padding-left: 24px;
                line-height: 51px;
                border-bottom: 1px solid #00000020;
            }
            ul{
                padding: 16px 24px 16px 24px;
            }
            li{
                display: flex;
                font-size: 20px;
                margin-bottom: 8px;
                img{
                    height: 18px;
                    width: 18px;
                    margin-right: 11px;
                }
            }
            a{
                color: #fff;
                background-color: #000;
                text-align: center;
                padding-top: 16px;
                padding-bottom: 16px;
            }
            a:hover{
                color: #000;
                background-color: gray;
            }
        }
    }
}